button {
    appearance: none;
    background-color: transparent;
    border: 0;
    cursor: pointer;
    display: inline-block;
    padding: 0;
    text-decoration: none;
    user-select: none;

    &:hover,
    &:focus {
        background-color: transparent;
        outline: none;
    }

    &:disabled {
        cursor: not-allowed;
        
        &:hover {
            background-color: transparent;
        }
    }
}

.button {
    padding: spacing(1) spacing(2);

    &--primary {

        &:hover {

        }

        &:focus {

        }

        &:disabled {
            
        }

    }
}


