*, ::after, ::before {
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
    height: 100%;
}

body {
    height: 100vh;
    display: flex;
    flex-direction: column;

    &.-nav-open {
        overflow: hidden;
    }
}

img,
picture {
    margin: 0;
    max-width: 100%;
}

.container {
    width: 100%;
    max-width: #{spacing(site-width) + (spacing(site-padding) * 2)};
    margin: 0 auto;
    padding: {
        right: spacing(site-padding);
        left: spacing(site-padding);
    }
}

.clearfix {
    @include clearfix;
}

.page-title {
    // For standard gencon page-title.
}

.page-main {
    flex: 1 0 auto;
    overflow: auto;
    padding-top: spacing(nav-height);
}

.editable-content {
    @include wysiwyg;
}

.breadcrumbs {
    @extend %list-reset-horiz;

    li {
        display: inline;
        padding-right: 5px;

        &:after {
            content: '>';
            padding-left: 5px;
        }

        &:last-child {

            &:after {
                display: none;
            }
        }
    }
}

.social-share {
    @extend %list-reset-horiz;
    text-align: center;

    li {
        margin-right: 10px;

        &:last-child {
            margin-right: 0;
        }
    }

    .a2a_img {
        display: none; //hide add2any's default images
    }
}

.responsive-video {

    &__container {
        position: relative;
        padding-bottom: 56.25%;
        padding-top: 25px;
        height: 0;

        > iframe {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }
}

