.sub-nav {
    position: relative;
    @include standard-transition(height);

    &__contents {
        background: rgba(palette(black), 0.2);
        padding: spacing(base) spacing(xs);
        @include media($bp-lg) {
            position: absolute;
            top: 100%;
            left: 0;
            z-index: 1;
        }

        &[data-property="closed"] {
            display: none;
        }
    }

    &__arrow {
        @include standard-transition(transform);
        right: 0;
        position: absolute;
        @include media($bp-lg) {
            position: relative;
        }
    }

    &__toggle[data-property="open"] &__arrow {
        transform: rotate(180deg);
    }

    &__item {
        white-space: nowrap;
    }
}


