/* Master Imports
======================================================================================= */
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
button, del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*
 * @name: @wysiwyg
 * @args: $margin-side, $margin-bottom, $image-max-width
 * @description: Applies to an element that contains WYSIWYG
 *               content. Gives sensible default margin values
 *               and max-width for floated image child elements.
 */
/*
 * @name: @standard-transition
 * @args: $properties, $duration, $easing, $delay
 * @description: Applies transition properties to an element
 *               with a 'standard' duration, easing, and delay.
 *               default usage: @include standard-transition(color background-color)
 *               Properties are added to the first argument separated
 *               by spaces. Next arguments set duration, easing and delay
 *               or use the default if omitted.
 */
/*
 * @name: %list-reset
 * @args: n/a
 * @description: Applies to a ul or ol element. Removes default
 *               list style, padding, and margin.
 */
.breadcrumbs, .social-share {
  list-style: none;
  margin: 0;
  padding: 0;
}

/*
 * @name: %list-reset-horiz
 * @args: n/a
 * @description: applies to a ul or ol element. Removes default
 *               list style, padding, and margin. Sets li child
 *               elements to disply: inline-block. Useful for
 *               navigation and category lists.
 */
.breadcrumbs li, .social-share li {
  display: inline-block;
}

/*
 * @name: %vertical-align
 * @args: n/a
 * @description: Vertically aligns an element in the center of
 *               its parent. Parent element must be of a fixed
 *               height. If the browser doesn't support transforms,
 *               it's a good idea to apply a fallback style that
 *               sets a reasonable top value.
 */
/* Breakpoints
=================================================================== */
