  background: #f00;
  font-size: 10px;
  line-height: 20px;
}

.slick-dots button:before {
  display: none;
}

.slick-dots .slick-active button {
  background: #ff0;
}

.main-nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.main-nav__mobile-toggle {
  display: block;
  font-size: 20px;
  height: 80px;
  overflow: visible;
  padding: 25px 20px;
  position: relative;
  transition-property: color;
  transition-duration: 150ms;
  transition-timing-function: ease;
  transition-delay: 0s;
}

@media screen and (min-width: 1100px) {
  .main-nav__mobile-toggle {
    display: none;
  }
}

.main-nav__mobile-toggle:hover:before {
  opacity: 1;
}

.main-nav__mobile-toggle:focus:before {
  opacity: 0;
}

.main-nav__contents {
  display: flex;
  align-items: flex-start;
  width: auto;
  margin-left: 0;
}

@media screen and (min-width: 1100px) {
  .main-nav__contents {
    width: 100%;
    margin-left: auto;
  }
}

.main-nav__list {
  display: none;
  flex-direction: column;
  max-height: calc(100vh - 80px);
  min-width: 300px;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  padding-right: 24px;
  padding-bottom: 24px;
  padding-left: 24px;
  position: absolute;
  right: 0;
  top: 100%;
  left: auto;
}

@media screen and (min-width: 1100px) {
  .main-nav__list {
    display: flex;
    background-color: transparent;
    flex-direction: row;
    max-height: auto;
    width: auto;
    overflow-y: visible;
    padding-right: 0;
    padding-bottom: 0;
    padding-left: 0;
    position: relative;
  }
}

.main-nav__list[data-property="open"] {
  display: flex;
}

@media screen and (min-width: 1100px) {
  .main-nav__item {
    margin-left: 24px;
  }
}

@media screen and (min-width: 1100px) {
  .main-nav__item--mobile {
    display: none;
  }
}

.main-nav__link {
  display: block;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 20px;
  font-weight: 800;
  width: 100%;
  line-height: 1;
  margin-bottom: 8px;
  padding: 8px;
  position: relative;
  text-align: left;
  transition-property: color, background;
  transition-duration: 150ms;
  transition-timing-function: ease;
  transition-delay: 0s;
}

@media screen and (min-width: 1100px) {
  .main-nav__link {
    font-size: 18px;
    margin-bottom: 0;
    padding-right: 0;
    padding-bottom: 8px;
    padding-left: 0;
  }
}

.main-nav__link--button {
  border: none;
  padding-top: 12px;
  padding-right: 32px;
  padding-bottom: 12px;
  padding-left: 32px;
}

.sub-nav {
  position: relative;
  transition-property: height;
  transition-duration: 150ms;
  transition-timing-function: ease;
  transition-delay: 0s;
}

.sub-nav__contents {
  background: rgba(0, 0, 0, 0.2);
  padding: 8px 16px;
}

@media screen and (min-width: 1100px) {
  .sub-nav__contents {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1;
  }
}

.sub-nav__contents[data-property="closed"] {
  display: none;
}

.sub-nav__arrow {
  transition-property: transform;
  transition-duration: 150ms;
  transition-timing-function: ease;
  transition-delay: 0s;
  right: 0;
  position: absolute;
}

@media screen and (min-width: 1100px) {
  .sub-nav__arrow {
    position: relative;
  }
}

.sub-nav__toggle[data-property="open"] .sub-nav__arrow {
  transform: rotate(180deg);
}

.sub-nav__item {
  white-space: nowrap;
}

.share {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.share__wrapper {
  position: relative;
  max-width: 200px;
  margin: 0 auto;
}

.share__list {
  backface-visibility: hidden;
  background-color: #000000;
  border-radius: 6px;
  display: none;
  justify-content: center;
  padding: 8px;
  position: absolute;
  margin: auto;
  left: 50%;
  top: calc(100% + 8px);
  z-index: 1;
  transform: translateX(-50%);
  transform-origin: top left;
}

.share__list > li {
  padding: 8px;
}

.share__list:before {
  content: '';
  height: 0;
  width: 0;
  border-bottom: 10px solid #000000;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  transform: translateY(-100%);
}

.share__button {
  background-color: #000000;
  color: white;
  position: relative;
}

.share__button:hover {
  background-color: #1a1919;
  color: white;
}

.share__button:focus {
  background-color: #333333;
  color: white;
}

.share__link {
  font-size: 40px;
  color: white;
}

.share__link:hover {
  color: #cccccc;
}

.share__link:focus, .share__link:active {
  color: #b3b2b2;
}

.page-header {
  background: rgba(0, 0, 0, 0.2);
  padding: 16px 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
}

.page-footer {
  flex-shrink: 0;
}

.frontend-test {
  max-width: 900px;
  padding: 0 20px;
}

.frontend-test .component-test {
  padding: 50px 0;
}

.frontend-test .component-test-heading {
  margin-top: 0;
}

.gallery-photoswipe {
  display: flex;
}



