.input-group {
    @include media($bp-mobile) {
        display: flex;
        width: 100%;
    }

    &__item {
        margin-bottom: spacing(xs);
        @include media($bp-mobile) {
        	flex: 1 1 auto;
        }

        &:last-child {
            margin-right: 0;
        }

        &--half {
            @include media($bp-mobile) {
            	margin-right: spacing(md);
                flex: 1 1 calc(50% - #{spacing(md) / 2});
            }
        }
    }
}


