// Fonts
$font-helvetica: Helvetica, Arial, sans-serif;

// Typography
$base-font-family: $font-helvetica;
$heading-font-family: $base-font-family;

// Font Sizes
$base-font-size: 16px;

// Line height
$base-line-height: 1.5;
$heading-line-height: 1.2;

// Other Sizes
$base-spacing: 8px;

$spacing: (
    base: $base-spacing,
    xs: $base-spacing * 2,
    sm: $base-spacing * 3,
    md: $base-spacing * 4,
    lg: $base-spacing * 5,
    nav-height: 66px,
    site-padding: $base-spacing * 3,
    site-width: 1200px
);

// Colors
$col-white: #ffffff;
$col-black: #000000;

$palette: (
    white: $col-white,
    black: $col-black
);

// Animations
$base-duration: 150ms;
$base-timing: ease;


