$bp-laptop    : new-breakpoint(min-width 1000px 24);
$bp-lg        : new-breakpoint(min-width 1100px 24);
$bp-xl        : new-breakpoint(min-width 1400px 24);
$bp-xxl       : new-breakpoint(min-width 1560px 24);


