.main-nav {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;

    &__mobile-toggle {
        display: block;
        font-size: 20px;
        height: 80px;
        overflow: visible;
        padding: 25px 20px;
        position: relative;
        @include standard-transition(color);
        @include media($bp-lg) {
            display: none;
        }

        &:hover,
        &:focus {

        }

        &:hover {
            &:before {
                opacity: 1;
            }
        }

        &:focus {
            &:before {
                opacity: 0;
            }
        }
    }

    &__contents {
        @extend .container;
        display: flex;
        align-items: flex-start;
        width: auto;
        margin-left: 0;
        @include media($bp-lg) {
            width: 100%;
            margin-left: auto;
        }
    }

    &__list {
        display: none;
        flex-direction: column;
        max-height: calc(100vh - 80px);
        min-width: 300px;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
        padding: {
            right: spacing(sm);
            bottom: spacing(sm);
            left: spacing(sm);
        }
        position: absolute;
        right: 0;
        top: 100%;
        left: auto;
        @include media($bp-lg) {
            display: flex;
            background-color: transparent;
            flex-direction: row;
            max-height: auto;
            width: auto;
            overflow-y: visible;
            padding: {
                right: 0;
                bottom: 0;
                left: 0;
            }
            position: relative;
        }

        &[data-property="open"] {
            display: flex;
        }
    }

    &__item {
        @include media($bp-lg) {
            margin-left: spacing(sm);
        }

        &--mobile {
            @include media($bp-lg) {
                display: none;
            }
        }
    }

    &__link {
        display: block;
        font: {
            family: $font-helvetica;
            size: 20px;
            weight: 800;
        }
        width: 100%;
        line-height: 1;
        margin-bottom: $base-spacing;
        padding: spacing(1);
        position: relative;
        text-align: left;
        @include standard-transition((color, background));
        @include media($bp-lg) {
            font-size: 18px;
            margin-bottom: 0;
            padding: {
                right: 0;
                bottom: 8px;
                left: 0;
            }
        }

        &:hover {
        }

        &--active {
        }

        &--button {
            border: none;
            padding: {
                top: 12px;
                right: spacing(md);
                bottom: 12px;
                left: spacing(md);
            }

            &:hover {
            }
        }
    }
}


